<!--suppress ALL -->
<template>
    <div class="animated fadeIn">
        <Row>
            <col :sm="24" :md="24" :lg="12">
            <h3></h3>
            <p></p>
            </col>
        </Row>
        <Table :columns="columns1" :data="data1"></Table>
        <div id="components-demo">
            <button-counter></button-counter>
        </div>

            <Select>
                <Option value="New York" label="New York">
                    <span>New York</span>
                    <span style="float:right;color:#ccc">America</span>
                </Option>
                <Option value="London" label="London">
                    <span>London</span>
                    <span style="float:right;color:#ccc">U.K.</span>
                </Option>
                <Option value="Sydney" label="Sydney">
                    <span>Sydney</span>
                    <span style="float:right;color:#ccc">Australian</span>
                </Option>
            </Select>

    </div>


</template>

<script>
    import  Vue from 'vue'
    Vue.component('button-counter', {
        data: function () {
            return {
                count: 0
            }
        },
        template: '<button v-on:click="count++">You clicked me {{ count }} times.</button>'
    })

    export default {
        name:'mr',
        data : ()=> {
            return {
                columns1: [
                    {
                        title: '',
                        key: 'name'
                    },
                    {
                        title: '',
                        key: 'age'
                    },
                    {
                        title: '',
                        key: 'address'
                    },
                    {
                        title: '',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (ce, params) =>
                        {



                           // console.info(params);
                            return ce('div',
                                [
                                    ce('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, ''),
                                ce('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '')
                            ]);
                        }
                    }

                ],
                data1: [
                    {
                        name: '',
                        age: 18,
                        address: ''
                    },
                    {
                        name: '',
                        age: 25,
                        address: ''
                    },
                    {
                        name: '',
                        age: 30,
                        address: ''
                    },
                    {
                        name: '',
                        age: 26,
                        address: ''
                    }
                ],
                cityList: [
                    {
                        value: 'New York',
                        label: 'New York'
                    },
                    {
                        value: 'London',
                        label: 'London'
                    },
                    {
                        value: 'Sydney',
                        label: 'Sydney'
                    },
                    {
                        value: 'Ottawa',
                        label: 'Ottawa'
                    },
                    {
                        value: 'Paris',
                        label: 'Paris'
                    },
                    {
                        value: 'Canberra',
                        label: 'Canberra'
                    }
                ]
            }
        },
        methods: {
            show (index) {
                this.$Modal.info({
                    title: '',
                    content: `${this.data1[index].name}<br>${this.data1[index].age}<br>${this.data1[index].address}`
                })
            },
            remove (index) {
                this.data1.splice(index, 1);
            }
        }
    }
</script>

<style scoped>

</style>


