/* eslint-disable no-unused-vars */
import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import './mock'; // mockjs
import './login.js';
import './ui.js';
import global_ from './components/Global'//
Vue.config.productionTip = false;

Vue.config.devtools = true
Vue.prototype.GLOBAL = global_//Vue
var vm = new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
});



