import Vue from 'vue';
import Router from 'vue-router';
import Full from '@/containers/Full';
import Full2 from '@/containers/Full2';
import mr from '@/views/mr';
import formMr from '@/views/formMr';
// Views - Pages
import Page404 from '@/views/errorPages/Page404';
import Page500 from '@/views/errorPages/Page500';
const _import = require('./_import_' + process.env.NODE_ENV);

/* login */
const Login = _import('login/index');
Vue.use(Router);

export const constantRouterMap = [
  { path: '/login', component: Login, hidden: true },
  {path: '/pages',
    redirect: '/pages/p404',
    name: 'Pages',
    component: {
      render (c) { return c('router-view') }
      // Full,
    },
    children: [{ path: '404', name: "Page404", component: Page404 },
      {path: '500', name: "Page500", component: Page500},

    ]
  }

]

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});

export const asyncRouterMap = [

  {
    path: '/',
    redirect: '/carouselMap',
    name: '',
    component: Full,
    hidden: false,
    children: [
      {path: '/carouselMap', name: '', icon: 'ios-images', component: _import('carouselMap')},
      {path: '/navigation', name: 'TV', icon: 'ios-alert', component: _import('navigation')},

      {path: '/goods', name: '', icon: 'ios-alert', component: _import('goods')},
      {path: '/goods/addFrom/:ids',
        name: "",
        component: _import('goods/addFrom'),
        props: true,
        hidden: true
      },
      {path: '/category', name: '', icon: 'ios-alert', component: _import('category'),
      },

      {path: '/category/addFrom',
        name: "",
        component: _import('category/addFrom'),
        hidden: true
      },
      {path: '/image', name: '', icon: 'ios-alert', component: _import('image')},
      /* {path: '/familyCombo', name: '', icon: 'ios-alert', component: _import('familyCombo')},
         {path: '/information', name: '', icon: 'ios-alert', component: _import('Information')},
          {path: '/introduction', name: '', icon: 'ios-alert', component: _import('Introduction')},
         {path: '/components',
           name: 'component',
           redirect: '/components/buttons',
           icon: 'ios-alert',
           component: {render (c) { return c('router-view') }},
           children: [{ path: 'buttons', name: 'Buttons', icon: 'ios-alert', component: _import('components/Buttons'), hidden: false, },
             {path: 'hoverbuttons', name: '', icon: 'ios-alert', component: _import('components/HoverButtons')},
             {path: 'alert', name: 'Alert', icon: 'ios-alert', component: _import('components/Alert')},
             {path: 'card', name: 'Card', icon: 'ios-alert', component: _import('components/Card')},
             {path: 'datepicker', name: 'DatePicker', icon: 'ios-alert', component: _import('components/DatePicker')},
             {path: 'form', name: 'Form', icon: 'ios-alert', component: _import('components/Form')},
             {path: 'modal', name: 'Modal', icon: 'ios-alert', component: _import('components/Modal')},
             {path: 'select', name: 'Select', icon: 'ios-alert', component: _import('components/Select')},
             {path: 'spin', name: 'Spin', icon: 'ios-alert', component: _import('components/Spin')},
             {path: 'steps', name: 'Steps', icon: 'ios-alert', component: _import('components/Steps')},
             {path: 'timeline', name: 'Timeline', icon: 'ios-alert', component: _import('components/Timeline')},
             {path: 'transfer', name: 'Transfer', icon: 'ios-alert', component: _import('components/Transfer')},
             {path: 'timepicker', name: 'Timepicker', icon: 'ios-alert', component: _import('components/Timepicker')},
             {path: 'upload', name: 'Upload', icon: 'ios-alert', component: _import('components/Upload')},
           ]
         },
         {path: '/charts',
           name: 'echart',
           redirect: '/charts/shopchart',
           icon: 'ios-alert',
           component: {render (c) { return c('router-view') }},
           children: [ { path: 'shopchart', name: '', icon: 'stats-bars', component: _import('charts/ShopChart'), hidden: false, },
             {path: 'radarchart', name: '', icon: 'arrow-graph-up-right', component: _import('charts/RadarChart')},
             {path: 'cakechart', name: '', icon: 'ios-analytics', component: _import('charts/CakeChart')}
           ]
         },
         {path: '/table', name: '', icon: 'ios-alert', component: _import('Table'), meta: { role: ['admin'] }},
         {path: '/jsontree', name: 'JSON', icon: 'ios-alert', component: _import('JsonTree')},
         {path: '/tabledetail/:id', name: 'TableDetail', hidden: true, component: _import('TableDetail')},
getCarouserAll
         {path: '/markdown', name: 'Markdown', icon: "ios-alert", component: _import('Markdown')},
         {path: '/mr', name: 'MR', icon: "ios-alert", component: mr},
         {path: '/formMr', name: 'FormMr', icon: "ios-alert", component: formMr},*/
      /* {path: '/tinymce', name: 'Tinymce', icon: "ios-alert", component: _import('Tinymce')},*/

    ]
  },
  { path: '*', redirect: '/pages/404', hidden: true },

];



